* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 1em;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.search-container {
  width: 100%;
  background-color: #3f57b3;
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.search-form {
  width: 100%;
  justify-content: center;
  display: flex;
}

.search-form input {
  width: 20%;
  font: inherit;
  background-color: #fff;
  border: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 8px 10px;
}

.search-form button {
  width: 2.5em;
  cursor: pointer;
  background-color: #c8c8c8;
  border: none;
  border-radius: 0 4px 4px 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form button:hover, .search-form button:focus {
  background-color: #b4b4b4;
  transition: all .3s;
}

.gallery-container {
  padding-bottom: 25px;
  padding-left: 15px;
  padding-right: 15px;
}

.gallery, .info {
  flex-wrap: wrap;
  display: flex;
}

.gallery {
  justify-content: center;
  margin-left: -20px;
  padding-top: 60px;
}

.gallery__link {
  min-width: 320px;
  border: 2px solid #f1eeee;
  border-radius: 10px;
  flex-basis: calc(25% - 20px);
  margin-top: 20px;
  margin-left: 20px;
  transition: all .3s;
  overflow: hidden;
}

.gallery__link:hover, .gallery__link:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.gallery-item {
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.gallery-item__img {
  height: 30vh;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-around;
  padding: 10px;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.info-item:not(:first-child) {
  margin-left: 5px;
}

.btn-load-more.is-hidden {
  display: none;
}

.btn-load-more {
  font: inherit;
  color: #fff;
  cursor: pointer;
  background-color: #3f57b3;
  border: none;
  border-radius: 5px;
  margin: 25px auto 0;
  padding: 8px 20px;
  display: block;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.btn-to-top {
  z-index: 1000;
  width: 50px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  background: #3f57b3;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 3vh;
  right: 1vw;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.btn-to-top--visible {
  opacity: 1;
  visibility: visible;
}

.btn-load-more:hover, .btn-to-top:hover {
  background-color: #2b3e88;
  transition: all .3s;
}

